:root {
  --bg-base: #020617;
  --bg-surface: #0f172a;
  --bg-card: #1e293b;
  --bg-hover: #334155;
  --primary: #06b6d4;
  --primary-dark: #0891b2;
  --primary-glow: rgba(6, 182, 212, 0.15);
  --primary-glow-strong: rgba(6, 182, 212, 0.3);
  --secondary: #6366f1;
  --secondary-glow: rgba(99, 102, 241, 0.15);
  --accent: #10b981;
  --accent-dim: #059669;
  --accent-glow: rgba(16, 185, 129, 0.15);
  --accent-blue: #38bdf8;
  --accent-amber: #f59e0b;
  --accent-red: #f87171;
  --accent-purple: #a855f7;
  --text-primary: #f1f5f9;
  --text-secondary: #94a3b8;
  --text-muted: #475569;
  --border: #1e293b;
  --border-subtle: rgba(148, 163, 184, 0.1);
  --border-focus: #06b6d4;
  --sidebar-w: 260px;
  --primary-rgb: 6, 182, 212;
  --secondary-rgb: 99, 102, 241;
  --accent-red-rgb: 248, 113, 113;
  --accent-amber-rgb: 245, 158, 11;
  --accent-emerald-rgb: 16, 185, 129;
  --accent-sky-rgb: 56, 189, 248;
  --accent-violet-rgb: 168, 85, 247;
  font-family: 'Space Grotesk', system-ui, sans-serif;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
html,
body {
  height: 100%;
  margin: 0;
}
body {
  background: var(--bg-base);
  color: var(--text-primary);
  font-family: 'Space Grotesk', system-ui, sans-serif;
  -webkit-font-smoothing: antialiased;
}
::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}
::-webkit-scrollbar-track {
  background: var(--bg-surface);
}
::-webkit-scrollbar-thumb {
  background: var(--bg-hover);
  border-radius: 3px;
}
::-webkit-scrollbar-thumb:hover {
  background: var(--text-muted);
}
h1,
h2,
h3,
h4 {
  font-weight: 600;
  line-height: 1.3;
  color: var(--text-primary);
}
code,
pre {
  font-family: 'JetBrains Mono', 'Fira Code', monospace;
}
.app-shell {
  display: grid;
  grid-template-columns: var(--sidebar-w) 1fr;
  grid-template-rows: 100vh;
  overflow: hidden;
}
.sidebar {
  background: var(--bg-surface);
  border-right: 1px solid var(--border);
  overflow-y: auto;
  display: flex;
  flex-direction: column;
}
.main-content {
  overflow-y: auto;
  display: flex;
  flex-direction: column;
}
.glass-panel {
  background: rgba(15, 23, 42, 0.6);
  -webkit-backdrop-filter: blur(12px);
  backdrop-filter: blur(12px);
  border: 1px solid var(--border-subtle);
}
.glass-card {
  background: linear-gradient(145deg, rgba(6, 182, 212, 0.05) 0%, rgba(15, 23, 42, 0.4) 100%);
  border: 1px solid rgba(6, 182, 212, 0.2);
  border-radius: 12px;
}
.glass-card-indigo {
  background: linear-gradient(145deg, rgba(99, 102, 241, 0.05) 0%, rgba(15, 23, 42, 0.4) 100%);
  border: 1px solid rgba(99, 102, 241, 0.2);
  border-radius: 12px;
}
.card {
  background: var(--bg-card);
  border: 1px solid var(--border);
  border-radius: 12px;
  padding: 20px;
}
.card:hover {
  border-color: var(--bg-hover);
  transition: border-color 150ms ease;
}
.btn {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 10px 20px;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 150ms ease;
  border: none;
  outline: none;
}
.btn-primary {
  background: var(--primary);
  color: #000;
}
.btn-primary:hover {
  background: var(--primary-dark);
  box-shadow: 0 0 20px var(--primary-glow-strong);
  transform: translateY(-1px);
}
.btn-primary:disabled {
  opacity: 0.4;
  cursor: not-allowed;
}
.btn-ghost {
  background: transparent;
  color: var(--text-secondary);
  border: 1px solid var(--border);
}
.btn-ghost:hover {
  background: var(--bg-hover);
  color: var(--text-primary);
}
.btn-danger {
  background: rgba(248, 113, 113, 0.1);
  color: var(--accent-red);
  border: 1px solid rgba(248, 113, 113, 0.3);
}
.btn-danger:hover {
  background: rgba(248, 113, 113, 0.2);
}
.input {
  width: 100%;
  background: var(--bg-surface);
  border: 1px solid var(--border);
  border-radius: 8px;
  padding: 10px 14px;
  font-size: 14px;
  color: var(--text-primary);
  outline: none;
  transition: border-color 150ms ease;
  font-family: inherit;
}
.input:focus {
  border-color: var(--border-focus);
  box-shadow: 0 0 0 3px var(--primary-glow);
}
.input::placeholder {
  color: var(--text-muted);
}
.badge {
  display: inline-flex;
  align-items: center;
  gap: 5px;
  padding: 2px 10px;
  border-radius: 999px;
  font-size: 11px;
  font-weight: 500;
}
.badge-running {
  background: rgba(6, 182, 212, 0.15);
  color: var(--primary);
  border: 1px solid rgba(6, 182, 212, 0.2);
}
.badge-completed {
  background: rgba(16, 185, 129, 0.15);
  color: var(--accent);
  border: 1px solid rgba(16, 185, 129, 0.2);
}
.badge-error {
  background: rgba(248, 113, 113, 0.15);
  color: var(--accent-red);
  border: 1px solid rgba(248, 113, 113, 0.2);
}
.badge-pending {
  background: rgba(100, 116, 139, 0.15);
  color: var(--text-secondary);
  border: 1px solid var(--border);
}
.status-dot {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 8px;
  height: 8px;
}
.status-dot::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  animation: ping 1.5s cubic-bezier(0, 0, 0.2, 1) infinite;
  background: currentColor;
  opacity: 0.7;
}
.status-dot::after {
  content: '';
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: currentColor;
}
.nav-item {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 8px 12px;
  border-radius: 8px;
  font-size: 13px;
  font-weight: 500;
  color: var(--text-secondary);
  text-decoration: none;
  transition: all 150ms ease;
  cursor: pointer;
}
.nav-item:hover {
  background: rgba(255, 255, 255, 0.05);
  color: var(--text-primary);
}
.nav-item.active {
  background: rgba(6, 182, 212, 0.1);
  color: var(--primary);
  border-left: 2px solid var(--primary);
  padding-left: 10px;
}
.event-message {
  padding: 14px 16px;
  border-left: 3px solid var(--accent);
  background: rgba(16, 185, 129, 0.05);
  border-radius: 0 8px 8px 0;
  animation: slideIn 200ms ease;
}
.event-thinking {
  padding: 12px 16px;
  border-left: 3px solid var(--accent-blue);
  background: rgba(56, 189, 248, 0.05);
  border-radius: 0 8px 8px 0;
  font-style: italic;
  color: var(--text-secondary);
  animation: slideIn 200ms ease;
}
.event-tool-call {
  padding: 12px 16px;
  border-left: 3px solid var(--accent-amber);
  background: rgba(245, 158, 11, 0.05);
  border-radius: 0 8px 8px 0;
  font-family: 'JetBrains Mono', monospace;
  font-size: 13px;
  animation: slideIn 200ms ease;
}
.event-error {
  padding: 12px 16px;
  border-left: 3px solid var(--accent-red);
  background: rgba(248, 113, 113, 0.05);
  border-radius: 0 8px 8px 0;
  animation: slideIn 200ms ease;
}
.dropzone {
  border: 2px dashed var(--border);
  border-radius: 12px;
  padding: 48px 24px;
  text-align: center;
  cursor: pointer;
  transition: all 200ms ease;
}
.dropzone:hover,
.dropzone.drag-over {
  border-color: var(--primary);
  background: var(--primary-glow);
}
.mode-toggle {
  display: flex;
  gap: 0;
  border: 1px solid var(--border);
  border-radius: 8px;
  overflow: hidden;
}
.mode-toggle button {
  flex: 1;
  padding: 8px 16px;
  background: transparent;
  border: none;
  color: var(--text-secondary);
  font-size: 13px;
  font-weight: 500;
  cursor: pointer;
  transition: all 150ms ease;
}
.mode-toggle button.active {
  background: var(--primary);
  color: #000;
}
.glow-text {
  text-shadow: 0 0 20px rgba(6, 182, 212, 0.5);
}
.glow-button {
  box-shadow: 0 0 15px var(--primary-glow-strong);
  transition: all 0.3s ease;
}
.glow-button:hover {
  box-shadow: 0 0 25px rgba(6, 182, 212, 0.5);
  transform: translateY(-1px);
}
.text-gradient {
  background: linear-gradient(135deg, #06b6d4 0%, #a855f7 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}
.text-gradient-green {
  background: linear-gradient(135deg, #10b981 0%, #06b6d4 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}
.page-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 32px;
  border-bottom: 1px solid var(--border);
  background: rgba(15, 23, 42, 0.8);
  -webkit-backdrop-filter: blur(12px);
  backdrop-filter: blur(12px);
  position: sticky;
  top: 0;
  z-index: 10;
}
.stat-card {
  background: rgba(15, 23, 42, 0.6);
  -webkit-backdrop-filter: blur(12px);
  backdrop-filter: blur(12px);
  border: 1px solid var(--border-subtle);
  border-radius: 16px;
  padding: 20px;
  position: relative;
  overflow: hidden;
}
.data-table {
  width: 100%;
  border-collapse: collapse;
  font-size: 13px;
}
.data-table th {
  padding: 12px 16px;
  text-align: left;
  font-size: 11px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  color: var(--text-muted);
  border-bottom: 1px solid var(--border);
  background: rgba(15, 23, 42, 0.4);
}
.data-table td {
  padding: 14px 16px;
  border-bottom: 1px solid rgba(30, 41, 59, 0.5);
  color: var(--text-secondary);
}
.data-table tr:hover td {
  background: rgba(255, 255, 255, 0.02);
}
@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(6px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes pulse {
  0%,
  100% {
    opacity: 1;
  }
  50% {
    opacity: 0.4;
  }
}
@keyframes ping {
  75%,
  100% {
    transform: scale(2);
    opacity: 0;
  }
}
@keyframes float {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-10px);
  }
  100% {
    transform: translateY(0px);
  }
}
@keyframes pulse-glow {
  0%,
  100% {
    box-shadow: 0 0 20px 0px rgba(6, 182, 212, 0.2);
  }
  50% {
    box-shadow: 0 0 35px 5px rgba(6, 182, 212, 0.4);
  }
}
@keyframes shimmer {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(100%);
  }
}
.animate-pulse-slow {
  animation: pulse 2s ease infinite;
}
.animate-float {
  animation: float 6s ease-in-out infinite;
}
.animate-float-delayed {
  animation: float 8s ease-in-out infinite;
  animation-delay: 2s;
}
.animate-pulse-glow {
  animation: pulse-glow 3s infinite;
}
details>summary {
  cursor: pointer;
  list-style: none;
}
details>summary::-webkit-details-marker {
  display: none;
}
.file-tree {
  font-size: 13px;
}
.file-tree-item {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 4px 8px;
  border-radius: 6px;
  cursor: pointer;
  color: var(--text-secondary);
  transition: all 100ms ease;
}
.file-tree-item:hover {
  background: var(--bg-hover);
  color: var(--text-primary);
}
.code-block {
  background: #0d1117;
  border: 1px solid var(--border);
  border-radius: 8px;
  padding: 16px;
  font-family: 'JetBrains Mono', monospace;
  font-size: 13px;
  overflow-x: auto;
  color: #e6edf3;
}
.pipeline-node {
  background: rgba(30, 41, 59, 0.8);
  -webkit-backdrop-filter: blur(8px);
  backdrop-filter: blur(8px);
  border: 1px solid var(--border);
  border-radius: 12px;
  padding: 16px;
  min-width: 160px;
  transition: all 200ms ease;
}
.pipeline-node.active {
  border-color: var(--primary);
  box-shadow: 0 0 20px rgba(6, 182, 212, 0.15);
}
.pipeline-node.completed {
  border-color: var(--accent);
  box-shadow: 0 0 20px rgba(16, 185, 129, 0.1);
}
.glass-card-indigo {
  background: linear-gradient(135deg, rgba(99, 102, 241, 0.12) 0%, rgba(15, 23, 42, 0.6) 100%);
  -webkit-backdrop-filter: blur(12px);
  backdrop-filter: blur(12px);
  border: 1px solid rgba(99, 102, 241, 0.2);
  position: relative;
  overflow: hidden;
}
.glass-card-indigo::before {
  content: '';
  position: absolute;
