  flex-direction: column;
  gap: 14px;
}
.outputs-root {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}
.outputs-page-title {
  font-size: 22px;
  font-weight: 700;
  color: #fff;
  margin: 0;
}
.outputs-page-subtitle {
  font-size: 13px;
  color: var(--text-muted);
  margin: 4px 0 0;
}
.outputs-header-actions {
  display: flex;
  gap: 8px;
}
.outputs-type-select {
  height: 38px;
  font-size: 13px;
}
.outputs-scroll {
  flex: 1;
  overflow-y: auto;
  padding: 32px;
}
.outputs-inner {
  max-width: 1100px;
  margin: 0 auto;
}
.outputs-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 16px;
}
.outputs-card {
  border-radius: 14px;
  padding: 20px;
}
.outputs-card[data-type="Notebook"] {
  --type-color: #6366f1;
}
.outputs-card[data-type="Model"] {
  --type-color: #a855f7;
}
.outputs-card[data-type="Dataset"] {
  --type-color: #06b6d4;
}
.outputs-card[data-type="Report"] {
  --type-color: #10b981;
}
.outputs-card[data-type="Chart"] {
  --type-color: #f59e0b;
}
.outputs-card-header {
  display: flex;
  gap: 12px;
  align-items: center;
  margin-bottom: 12px;
}
.outputs-icon-box {
  width: 40px;
  height: 40px;
  border-radius: 10px;
  background: var(--type-color);
  @supports (color: color-mix(in lab, red, red)) {
    background: color-mix(in srgb, var(--type-color) 8%, transparent);
  }
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--type-color);
  flex-shrink: 0;
}
.outputs-icon {
  font-size: 22px;
}
.outputs-name-wrap {
  min-width: 0;
}
.outputs-file-name {
  font-size: 13px;
  font-weight: 600;
  color: #fff;
  margin: 0 0 2px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.outputs-type-badge {
  padding: 1px 7px;
  border-radius: 3px;
  font-size: 10px;
  font-weight: 700;
  background: var(--type-color);
  @supports (color: color-mix(in lab, red, red)) {
    background: color-mix(in srgb, var(--type-color) 8%, transparent);
  }
  color: var(--type-color);
}
.outputs-meta-row {
  display: flex;
  gap: 12px;
  margin-bottom: 14px;
}
.outputs-meta-text {
  font-size: 11px;
  color: var(--text-muted);
}
.outputs-session-id {
  color: #06b6d4;
  font-family: 'JetBrains Mono', monospace;
}
.outputs-action-row {
  display: flex;
  gap: 8px;
}
.outputs-download-btn {
  flex: 1;
  font-size: 12px;
  padding: 6px 0;
}
.outputs-download-icon {
  font-size: 15px;
}
.outputs-delete-btn {
  background: none;
  border: none;
  cursor: pointer;
  color: #f87171;
  padding: 6px 8px;
}
.outputs-delete-icon {
  font-size: 18px;
}
.arch-root {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}
.arch-page-title {
  font-size: 22px;
  font-weight: 700;
  color: #fff;
  margin: 0;
}
.arch-page-subtitle {
  font-size: 13px;
  color: var(--text-muted);
  margin: 4px 0 0;
}
.arch-scroll {
  flex: 1;
  overflow-y: auto;
  padding: 32px;
}
.arch-inner {
  max-width: 1100px;
  margin: 0 auto;
}
.arch-diagram {
  border-radius: 20px;
  padding: 40px;
  margin-bottom: 32px;
  min-height: 420px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
}
.arch-orchestrator {
  text-align: center;
  margin-bottom: 40px;
}
.arch-orch-icon-box {
  width: 72px;
  height: 72px;
  border-radius: 20px;
  background: rgba(99, 102, 241, 0.2);
  border: 2px solid #6366f1;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 10px;
  box-shadow: 0 0 24px rgba(99, 102, 241, 0.3);
}
.arch-orch-icon {
  font-size: 36px;
  color: #6366f1;
}
.arch-orch-label {
  font-size: 14px;
  font-weight: 700;
  color: #fff;
}
.arch-orch-desc {
  font-size: 12px;
  color: var(--text-muted);
  margin-top: 4px;
}
.arch-connector {
  position: absolute;
  top: 140px;
  left: 50%;
  width: 2px;
  height: 40px;
  background: var(--border);
  transform: translateX(-50%);
}
.arch-workers-row {
  display: flex;
  gap: 32px;
  flex-wrap: wrap;
  justify-content: center;
  position: relative;
}
.arch-worker {
  text-align: center;
}
.arch-worker-icon-box {
  width: 56px;
  height: 56px;
  border-radius: 14px;
  background: rgba(0, 0, 0, 0.08);
  @supports (background: color-mix(in srgb, red, blue)) {
    background: color-mix(in srgb, var(--agent-color) 8%, transparent);
  }
  border: 1.5px solid rgba(255, 255, 255, 0.2);
  @supports (border: 1.5px solid color-mix(in srgb, red, blue)) {
    border: 1.5px solid color-mix(in srgb, var(--agent-color) 32%, transparent);
  }
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 8px;
  color: var(--agent-color);
}
.arch-worker-icon {
  font-size: 28px;
}
.arch-worker-label {
  font-size: 12px;
  font-weight: 600;
  color: var(--text-secondary);
}
.arch-worker[data-agent="Cleaner"] {
  --agent-color: #06b6d4;
  --agent-color-rgb: 6, 182, 212;
}
.arch-worker[data-agent="EDA Bot"] {
  --agent-color: #f59e0b;
  --agent-color-rgb: 245, 158, 11;
}
.arch-worker[data-agent="Engineer"] {
  --agent-color: #10b981;
  --agent-color-rgb: 16, 185, 129;
}
.arch-worker[data-agent="Model Gen"] {
  --agent-color: #a855f7;
  --agent-color-rgb: 168, 85, 247;
}
.arch-worker[data-agent="Reviewer"] {
  --agent-color: #38bdf8;
  --agent-color-rgb: 56, 189, 248;
}
.arch-worker[data-agent="Reporter"] {
  --agent-color: #fb923c;
  --agent-color-rgb: 251, 146, 60;
}
.arch-table-panel {
  border-radius: 16px;
  overflow: hidden;
}
.arch-table-header {
  padding: 16px 20px;
  border-bottom: 1px solid var(--border);
}
.arch-table-title {
  font-size: 16px;
  font-weight: 700;
  color: #fff;
  margin: 0;
}
.arch-table-scroll {
  overflow-x: auto;
}
.arch-agent-name {
  font-weight: 600;
  color: #fff;
}
.arch-cap-yes {
  text-align: center;
  color: #10b981;
  font-weight: 700;
}
.arch-cap-no {
  text-align: center;
  color: var(--text-muted);
  font-weight: 400;
}
.agents-root {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}
.agents-page-title {
  font-size: 22px;
  font-weight: 700;
  color: #fff;
  margin: 0;
}
.agents-page-subtitle {
  font-size: 13px;
  color: var(--text-muted);
  margin: 4px 0 0;
}
.agents-header-actions {
  display: flex;
  gap: 8px;
}
.agents-header-link {
  text-decoration: none;
}
.agents-btn-icon {
  font-size: 18px;
}
.agents-scroll {
  flex: 1;
  overflow-y: auto;
  padding: 32px;
}
.agents-inner {
  max-width: 1200px;
  margin: 0 auto;
}
.agents-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  gap: 20px;
}
.agents-card {
