  margin-bottom: 16px;
}
.agents-card-icon-box {
  width: 44px;
  height: 44px;
  border-radius: 12px;
  background: rgba(var(--agent-color-rgb), 0.12);
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--agent-color);
}
.agents-card-icon {
  font-size: 24px;
}
.agents-card-name {
  font-size: 16px;
  font-weight: 700;
  color: #fff;
  margin-bottom: 6px;
}
.agents-card-desc {
  font-size: 13px;
  color: var(--text-secondary);
  line-height: 1.5;
  margin-bottom: 16px;
}
.agents-card-footer {
  display: flex;
  justify-content: space-between;
  padding: 12px 0;
  border-top: 1px solid var(--border);
}
.agents-stat {
  text-align: center;
}
.agents-stat-value {
  font-size: 18px;
  font-weight: 700;
  color: #fff;
}
.agents-stat-value-success {
  font-size: 18px;
  font-weight: 700;
  color: #10b981;
}
.agents-stat-label {
  font-size: 11px;
  color: var(--text-muted);
  text-transform: uppercase;
  letter-spacing: 0.05em;
}
.agents-configure-btn {
  background: none;
  border: 1px solid rgba(var(--agent-color-rgb), 0.25);
  border-radius: 8px;
  padding: 6px 14px;
  color: var(--agent-color);
  font-size: 13px;
  cursor: pointer;
}
.agents-card[data-agent-id="orchestrator"] {
  --agent-color: #6366f1;
  --agent-color-rgb: 99, 102, 241;
}
.agents-card[data-agent-id="cleaner"] {
  --agent-color: #06b6d4;
  --agent-color-rgb: 6, 182, 212;
}
.agents-card[data-agent-id="eda"] {
  --agent-color: #f59e0b;
  --agent-color-rgb: 245, 158, 11;
}
.agents-card[data-agent-id="engineer"] {
  --agent-color: #10b981;
  --agent-color-rgb: 16, 185, 129;
}
.agents-card[data-agent-id="model-gen"] {
  --agent-color: #a855f7;
  --agent-color-rgb: 168, 85, 247;
}
.agents-card[data-agent-id="reviewer"] {
  --agent-color: #38bdf8;
  --agent-color-rgb: 56, 189, 248;
}
.agents-card[data-agent-id="reporter"] {
  --agent-color: #fb923c;
  --agent-color-rgb: 251, 146, 60;
}
.agents-card[data-agent-id="security"] {
  --agent-color: #f87171;
  --agent-color-rgb: 248, 113, 113;
}
.agents-status-active {
  padding: 3px 10px;
  border-radius: 999px;
  font-size: 11px;
  font-weight: 600;
  background: rgba(16, 185, 129, 0.1);
  color: #10b981;
  border: 1px solid rgba(16, 185, 129, 0.2);
}
.agents-status-idle {
  padding: 3px 10px;
  border-radius: 999px;
  font-size: 11px;
  font-weight: 600;
  background: rgba(100, 116, 139, 0.1);
  color: var(--text-muted);
  border: 1px solid var(--border);
}
.pipe-root {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}
.pipe-page-title {
  font-size: 22px;
  font-weight: 700;
  color: #fff;
  margin: 0;
}
.pipe-page-subtitle {
  font-size: 13px;
  color: var(--text-muted);
  margin: 4px 0 0;
}
.pipe-header-right {
  display: flex;
  gap: 8px;
  align-items: center;
}
.pipe-live-dot {
  display: inline-block;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: #10b981;
  box-shadow: 0 0 8px #10b981;
}
.pipe-live-label {
  font-size: 12px;
  color: #10b981;
  font-family: 'JetBrains Mono', monospace;
}
.pipe-scroll {
  flex: 1;
  overflow-y: auto;
  padding: 32px;
}
.pipe-inner {
  max-width: 900px;
  margin: 0 auto;
}
.pipe-stages {
  position: relative;
}
.pipe-stage-row {
  display: flex;
  gap: 20px;
  margin-bottom: 0;
}
.pipe-stage-row[data-stage="Data Ingestion"] {
  --stage-color: #10b981;
}
.pipe-stage-row[data-stage="Auto-Cleaning"] {
  --stage-color: #06b6d4;
}
.pipe-stage-row[data-stage="EDA"] {
  --stage-color: #6366f1;
}
.pipe-stage-row[data-stage="Feature Engineering"] {
  --stage-color: #f59e0b;
}
.pipe-stage-row[data-stage="Model Training"] {
  --stage-color: #a855f7;
}
.pipe-stage-row[data-stage="Peer Review"] {
  --stage-color: #38bdf8;
}
.pipe-stage-row[data-stage="Report Generation"] {
  --stage-color: #fb923c;
}
.pipe-connector-col {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 48px;
}
.pipe-step-icon {
  width: 44px;
  height: 44px;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  z-index: 1;
}
.pipe-step-icon[data-status="completed"],
.pipe-step-icon[data-status="running"] {
  background: rgba(var(--stage-color-rgb), 0.12);
  border: 2px solid var(--stage-color);
  color: var(--stage-color);
}
.pipe-step-icon[data-status="pending"] {
  background: var(--bg-card);
  border: 2px solid var(--border);
  color: var(--text-muted);
}
.pipe-step-icon-glyph {
  font-size: 22px;
}
.pipe-step-icon-glyph-spin {
  font-size: 22px;
  animation: pulse 1.5s infinite;
}
.pipe-connector-line {
  width: 2px;
  flex: 1;
  min-height: 24px;
  margin-top: 0;
}
.pipe-connector-line[data-filled="true"] {
  background: var(--stage-color);
}
.pipe-connector-line[data-filled="false"] {
  background: var(--border);
}
.pipe-card {
  flex: 1;
  border-radius: 14px;
  padding: 16px 20px;
  margin-bottom: 12px;
}
.pipe-card[data-status="running"] {
  border: 1px solid rgba(var(--stage-color-rgb), 0.38);
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
  box-shadow: 0 0 20px rgba(var(--stage-color-rgb), 0.12);
}
.pipe-card[data-status="completed"],
.pipe-card[data-status="pending"] {
  border: 1px solid var(--border);
  box-shadow: none;
}
.pipe-card-top {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
.pipe-card-name {
  font-size: 15px;
  font-weight: 700;
  margin-bottom: 4px;
}
.pipe-card-name[data-status="pending"] {
  color: var(--text-secondary);
}
.pipe-card-name[data-status="completed"],
.pipe-card-name[data-status="running"] {
  color: #fff;
}
.pipe-card-agent {
  font-size: 12px;
  color: var(--text-muted);
}
.pipe-card-agent-name[data-status="pending"] {
  color: var(--text-muted);
}
.pipe-card-agent-name[data-status="completed"],
.pipe-card-agent-name[data-status="running"] {
  color: var(--stage-color);
}
.pipe-card-meta {
  display: flex;
  gap: 8px;
  align-items: center;
}
.pipe-card-duration {
  font-family: 'JetBrains Mono', monospace;
  font-size: 12px;
  color: var(--text-muted);
}
.pipe-status-completed {
  padding: 2px 10px;
  border-radius: 999px;
  font-size: 11px;
  font-weight: 600;
  text-transform: capitalize;
  background: rgba(16, 185, 129, 0.1);
  color: #10b981;
}
.pipe-status-running {
  padding: 2px 10px;
  border-radius: 999px;
  font-size: 11px;
  font-weight: 600;
  text-transform: capitalize;
  background: var(--stage-color);
  @supports (color: color-mix(in lab, red, red)) {
    background: color-mix(in srgb, var(--stage-color) 12%, transparent);
  }
  color: var(--stage-color);
}
.pipe-status-pending {
  padding: 2px 10px;
  border-radius: 999px;
  font-size: 11px;
  font-weight: 600;
  text-transform: capitalize;
  background: rgba(100, 116, 139, 0.1);
  color: var(--text-muted);
}
.pipe-progress {
  margin-top: 12px;
}
.pipe-progress-header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 6px;
}
.pipe-progress-label {
  font-size: 12px;
  color: var(--text-muted);
}
.pipe-progress-value {
  font-size: 12px;
  color: var(--stage-color);
  font-weight: 600;
}
.pipe-progress-track {
  width: 100%;
  height: 6px;
  border-radius: 999px;
  background: var(--bg-hover);
  overflow: hidden;
}
.pipe-progress-fill {
  height: 100%;
  border-radius: 999px;
  background: var(--stage-color);
  transition: width 300ms;
}
.dash-root {
